import E404 from "../../assets/images/error404.jpg";
import { FotoError, TituloPrincipal } from "../UI"


function Error404() {
    return(
        <div>
            <a href="/"><FotoError src={E404} alt="Error 404" title="Ocurrio un error"/></a>
            <a href="/"><TituloPrincipal>Volvemos?</TituloPrincipal></a>
        </div>
    );
}

export default Error404;