import styled  from "styled-components"
import Foto_cv from "../../assets/images/foto_cv_redonda.png"
import FotoGitHub from "../../assets/images/github.png"
import FotoLinkedIn from "../../assets/images/linkedin.png"
import FotoLogoCF from "../../assets/images/LOGO_CF_limpio_ch.png"
import Curriculum from "../../assets/docs/cristian_fontanini.pdf"
import { ParrafoPrincipal, CajaHola, CajaFotoH3, TituloPrincipal, CajaTitulo, CajaLinks, LinkIndividual, DivImg } from "../UI"




const ArchivoDescarga = () => {
    return (
      <LinkIndividual href={Curriculum} download>
        <img src={FotoLogoCF} alt="Curriculum CF" title="Curriculum" />
      </LinkIndividual>
    );
  };

  

export const Links = () => {
    return (
        <CajaLinks>
            <LinkIndividual href="https://www.linkedin.com/in/cristian-fontanini/" target="_blank" rel="noopener noreferrer">
                <img src={FotoLinkedIn} alt="Foto LikedIn" title="LinkedIn"/>
            </LinkIndividual>
            <LinkIndividual href="https://github.com/Cfonta1986" target="_blank" rel="noopener noreferrer">
                <img src={FotoGitHub} alt="Foto GitHub" title="GitHub" />
            </LinkIndividual>
            <ArchivoDescarga />
        </CajaLinks>
    );
};

const Hola = () => {
    return (
        <CajaHola>
            <CajaFotoH3>
                <DivImg>
                    <img src={Foto_cv} alt="Foto de Cristian Fontanini" title="Cristian Fontanini" />
                </DivImg>
                <CajaTitulo>
                    <TituloPrincipal>Hola!!! <br /> Mi nombre es Cristin Fontanini.</TituloPrincipal>
                    <ParrafoPrincipal>Construyo PAGINAS WEB, me encanta la DATA SCIENCE y soy bombero. Me form en Marketing Digital en Google Activate, Diseo de Pginas Web con Wordpress, Desarrollador FRONT-END y Python para Data Science en Alura / Oracle y Argentina Programa 4.0. </ParrafoPrincipal>
                    <Links />
                </CajaTitulo>               
            </CajaFotoH3>
        </CajaHola>
    )
}

export default Hola;