import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { CajaForm, NuevoInput, ContainedButton } from "../UI"
import emailjs from 'emailjs-com';


const FormContacto = () => {
  const { register, handleSubmit, formState: { errors } } = useForm();

const enviarCorreo = (data) => {
  emailjs.send('service_5gjx3hi', 'template_zl4kjvi', data, 'FiW-kEWOssBQIGo4i')
    .then((response) => {
      console.log('Correo electrnico enviado exitosamente:', response);
    }, (error) => {
      console.error('Error al enviar el correo electrnico:', error);
    });
};
const onSubmit = (data) => {
  enviarCorreo(data);
};

  const [inputError, setInputError] = useState({
    nombre: false,
    eMail: false,
    asunto: false,
    mensaje: false,
  });

  const handleBlur = (nombreInput) => {
    setInputError(prevState => ({
      ...prevState,
      [nombreInput]: errors[nombreInput] ? true : false,
    }));
  };

  return (
    <CajaForm onSubmit={handleSubmit(onSubmit)}>
      <NuevoInput 
        aria-label="Nombre" 
        autoComplete="name" 
        type="text" 
        placeholder="Nombre"
        multiline 
        name="Nombre"
        {...register("nombre", { required: true, minLength: 3 })}
        error={inputError.nombre}
        onBlur={() => handleBlur("nombre")} 
      />
      <NuevoInput 
        type="email" 
        autoComplete="email" 
        placeholder="E-Mail" 
        multiline 
        name="email"
        {...register("eMail", { required: true, pattern: {
          value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
          message: "Direccin de E-Mail no vlida",
        }, })} 
        error={inputError.eMail}
        onBlur={() => handleBlur("eMail")} />
      <NuevoInput 
        aria-label="Asunto" 
        type="text" 
        placeholder="Asunto" 
        multiline 
        name="Asunto"
        {...register("asunto", { required: true, minLength: 3 })}
        error={inputError.asunto}
        onBlur={() => handleBlur("asunto")} 
      />
      <NuevoInput 
        aria-label="Mensaje" 
        label="Mensaje" 
        placeholder="Mensaje" 
        multiline 
        name="Mensaje"
        {...register("mensaje", { required: true, minLength: 3 })}
        error={inputError.mensaje}
        onBlur={() => handleBlur("mensaje")} 
      />

      <ContainedButton type="submit" size="large" variant="contained">Enviar</ContainedButton>
    </CajaForm>
  );
}


export default FormContacto;